@import "../../styles/variables.scss";

footer {
	background-color: $dark;
	text-align: center;
	font-size: 2rem;
}


.footer-copy {
	color: $secundary;
	font-size: 0.7rem;
	padding: 1rem;
	padding-top: 3rem;
	padding-bottom: 3rem;
}

@media only screen and (min-width: 176px) and (max-width: 600px) {
	div.footer-sentence {
		margin-right: 1rem;
		margin-left: 1rem;
	}
	.footer-sentence-icon {
		display: none;
	}
	div.child:first-child {
		text-align: center;
		padding: 0.5rem;
	}
}

